#root{width:100%; height:100%; overflow: hidden; }
.loginContainer{width:100%; height:100%; overflow: hidden;}
.video{width:100%; height:auto; min-height: 100%; opacity: 1.0; min-width:100%;}
.loginLogo{position: absolute; z-index: 2; top:30px; left:30px; width: 180px; text-align: center; color:#fff; font-size: 16px; line-height: 30px;}
.loginLogo img{width:120px;}
.login{position: absolute; top:50%; left:50%; z-index: 1; background: url("../images/login.png") no-repeat center center; width: 372px; height:340px; margin-left:-190px;}
.loginWrapper{margin: 20px 25px 20px; width: 322px; height: 340px; box-sizing: border-box; }
.loginWrapper h1{color:#50a4f4; text-align: center;  line-height: 30px; margin: 15px 0 50px; float: left; width: 100%; }
.loginWrapper h1 span{text-align: center; width: 50%; float: left; font-size: 16px; color:#e6e6e6; border-right:1px solid #e6e6e6; box-sizing: border-box; cursor:pointer;}
.loginWrapper h1 span.cur{color:#00a2e9; font-weight: bold; font-size: 26px;}
.loginWrapper h1 span.cur1{color:#00a2e9; font-weight: bold; font-size: 26px; width: 100%;}
.loginWrapper h1 span:last-child{border:0}
.loginWrapper input{line-height: 40px; margin-bottom: 40px; height: 40px; background: rgba(68,105,134,0.36);  box-sizing: border-box; border:1px solid #446986; width: 100%; padding:0 1em; color: #fff; font-size: 16px;}
.loginWrapper button{border:none;background: none; text-align: center; width: 100%; font-size: 22px; color: #fff; font-weight: 300; outline: none; line-height: 30px;}
.loginWrapper button i{display: inline-block; background: url("../images/loginIcon.png") no-repeat center center; width:25px; height:25px; vertical-align: middle; margin-left: 15px;}
.IndexHome{width:100%; position:absolute; top:100px; left:0; bottom:0;}

.header{width:100%;height:100px; background: #000; display: flex; padding: 0 0.5%; box-sizing: border-box; color:#01f8ff;}
.HeaderLeft{float:left; background: url("../images/title_left.png") no-repeat left center; width: 480px; font-size: 30px; text-indent: 100px; line-height: 78px;}
.HeaderLeft i{position: absolute; z-index: 2; background: url("../images/big.png") no-repeat center center; width: 58px; height: 58px; top:39px; left:32px;  animation:turn 1.5s linear infinite; }
@keyframes turn{
    0%{-webkit-transform:rotate(0deg);}
    25%{-webkit-transform:rotate(90deg);}
    50%{-webkit-transform:rotate(180deg);}
    75%{-webkit-transform:rotate(270deg);}
    100%{-webkit-transform:rotate(360deg);}
}
.HeaderContent{flex: 1; background: url("../images/title_content.png") repeat-x left center; position: relative;}
.HeaderContentHome{flex: 1; background: url("../images/title_content1.png") repeat-x left center;}
.HeaderContentHomeNav{width: 100%; position: relative; line-height: 118px; display: flex;}
.HeaderContentHomeNav li{flex: 1; line-height: 98px; text-align: center; font-size: 20px;color:#fff;font-weight: 100;cursor:pointer;}
.HeaderContentHomeNav li:hover,.HeaderContentHomeNav li.active{background:url("../images/bg_xz.png") no-repeat center center;}
.HeaderContent li,.HeaderContentHome li{float:left;margin: 35px 10px;width: 118px;text-align: center;line-height: 34px;font-size: 20px;color:#fff;font-weight: 100;cursor:pointer;}
.HeaderContent li:hover,.HeaderContent li.cur,.HeaderContent li.active,.HeaderContentHome li:hover,.HeaderContentHome li.cur,.HeaderContentHome li.active{background:url("../images/bg_xz.png") no-repeat center center;}
.HeaderContent li.liItem{margin-left: -128px;}
.HeaderRight{float:right; background: url("../images/title_right.png") no-repeat right center; width: 292px; position: relative;}
.HeaderRightHome{float:right; background: url("../images/title_right1.png") no-repeat left center; width: 520px; position: relative;}
.HeaderRight em,.HeaderRightHome em{position: absolute; z-index: 2; background: url("../images/small.png") no-repeat center center; width: 44px; height: 44px; animation:turn 1.5s linear infinite;}
.HeaderRight em{top:48px; left:33px;}
.HeaderRightHome em{top:48px; left:50px;}
.HeaderTime{float: left; line-height: 100px; font-size: 20px; margin-left: 120px;}
.HeaderUser{ float:right; margin-top: 18px; text-align: center; font-size: 16px; line-height: 28px; color:#fff;}

.HeaderUser i{display: block;  font-size: 24px; ,font-weight: bold; color:#01f8ff;}
.HeaderBank{background: url("../images/Bank.png") no-repeat; width: 32px; height:32px; float:right; margin: 34px 30px;}

.left{float:left; width: 0.5%; height:100%; background: #000;}
.right{float:right; width: 0.5%; height:100%; background: #000;}
.conent{float:left; width: 99%; height: 100%; background: #000; box-sizing: border-box; border:3px solid #0b406f; overflow:hidden;}

.footer{position: absolute;bottom:0;width:80%;z-index:1; left:10%;}
.footerContent{margin:0 auto; width: 540px; display: flex; height: 95px;}
.footer li{flex: 1; color:#fff;font-size:14px;text-align:center;line-height:25px;cursor:pointer; background:url("../images/foot.png") no-repeat top center;}
.footer li:hover,.footer li:active,.footer li.active span{color:#3b86f7;}
.footer li i{margin:20px auto 0;display:block;overflow:hidden;width:24px;height:24px;background-size:100% 100%;}
.footer li i.icon1{background: url("../images/foot/icon1.png") no-repeat center center;}
.footer li:hover i.icon1,.footer li:active i.icon1{background: url("../images/foot/icon1_xz.png") no-repeat center center;}
.footer li i.icon2{background: url("../images/foot/icon2.png") no-repeat center center;}
.footer li:hover i.icon2,.footer li:active i.icon2,.footer li.active i.icon2{background: url("../images/foot/icon2_xz.png") no-repeat center center;}
.footer li i.icon3{background: url("../images/foot/icon3.png") no-repeat center center;}
.footer li:hover i.icon3,.footer li:active i.icon3,.footer li.active i.icon3{background: url("../images/foot/icon3_xz.png") no-repeat center center;}
.footer li i.icon4{background: url("../images/foot/icon4.png") no-repeat center center;}
.footer li:hover i.icon4,.footer li:active i.icon4,.footer li.active i.icon4{background: url("../images/foot/icon4_xz.png") no-repeat center center;}
.footer li i.icon5{background: url("../images/foot/icon5.png") no-repeat center center;}
.footer li:hover i.icon5,.footer li:active i.icon5{background: url("../images/foot/icon5_xz.png") no-repeat center center;}
/*常用位置 Position*/
.PositionWrapper{position:absolute;left:50%;top:50%;margin-left:-550px;margin-top:-213px;width:1100px;height:160px;display: flex;justify-content: center}
.PositionLeft{height:160px; background:url("../images/foot/position.png") no-repeat center center; background-size: 100% 100%;}
#PositionList{margin-left:23px;height:160px;}
/*.PositionList::-webkit-scrollbar{display: none;}*/
.PositionListItem{float:left;width:116px;height:108px;margin:25px 20px 25px 0;display: inline-block;border:1px solid #69b2e5;border-radius:15px;padding:0 20px;}
.PositionListItem span{ display:block;width:100%;color:#f7ad27;font-size:16px;line-height:30px;height:35px;}
.PositionListItem span em{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;display:inline-block;width:3em;}
.PositionListItem span i{float:right;width:20px;height:20px;margin-top:5px;}
.PositionListItem span i.item1{ background:url("../images/foot/delete.png") no-repeat center center;}
.PositionListItem span i.item2{ margin-right:5px;background:url("../images/foot/push.png") no-repeat center center;}
.PositionListItem img{  width:120px;height:60px;}
.PositionRight{width:150px;height:160px;margin-left:30px;background: url("../images/foot/add.png") no-repeat center center;color:#fff;text-align:center;font-size:14px;line-height:20px;}
.PositionRight i{display:block;width:32px;height:32px;margin:50px 59px 10px;background: url("../images/foot/addPic.png") no-repeat center center;}
/*更多 more*/
.MoreShow{position:absolute;left:50%;top:50%;margin-left:-213px;margin-top:-128px;width:426px;height:76px;display:flex;background: url("../images/foot/More.png") no-repeat center center;}
.addMore{left:50%;top:50%;margin-left:-103px;margin-top:-128px; width: 426px; height: 76px; position:absolute; text-align: center;}
.MoreItem{float:left;flex:1; width:auto;color:#fff;font-size:14px;text-align:center;line-height:25px;cursor:pointer;}
.MoreCur{display: flex; left:0; top:-30px; margin: 0 auto; width:auto; border-width: 20px 30px 20px 30px; background: none; border-style: solid; box-sizing: border-box;
    border-image: url("../images/foot/More.png") 20 30 20 30 fill round; overflow-x: auto;overflow-y: hidden;white-space: nowrap; height: 100px;}
.MoreCur::-webkit-scrollbar {height: 10px;position: absolute;}
.MoreCur::-webkit-scrollbar-track {background: rgba(128,128,128,0.5);border-radius: 5px;}
.MoreCur::-webkit-scrollbar-thumb {background: rgba(77,156,227,0.6);border-radius: 10px;}
.MoreCur::-webkit-scrollbar-thumb:hover {background: rgba(77,156,227,0.6);}
.MoreCur::-webkit-scrollbar-corner {background: #179a16;}
.MoreCur .MoreItem{margin: 0 13px;}
.MoreItem:hover,.MoreItem span.active{color:#3b86f7;}
.MoreItem i{margin:12px auto 0;display:block;overflow:hidden;width:24px;height:24px;background-size:100% 100%;}
.MoreItem img{margin:5px auto 0;display:block;overflow:hidden;width:24px;height:24px;background-size:100% 100%;
    -webkit-filter:brightness(100);filter: brightness(100);
}
.MoreItem img.active{
    -webkit-filter: hue-rotate(235deg);filter: hue-rotate(235deg);
}

.MoreItem i.more1{background: url("../images/foot/tc.png") no-repeat center center;}
