.AddImport .PositionFloor{float: left; color:#fff;}
.AddImport .PositionFloor span{float: left;}
.AddImport .PositionFloor span input[type="checkbox"]{width: 20px; height: 20px; background-size: 100% 100%; margin: 5px 0;}
.AddImport .PositionFloor span select{width: 55px;}
.AddImport li{line-height: 30px;}
.AddImport li span{margin: 0;}
.TwoDataNav{float:left; width: 200px; height: 30px; line-height: 30px; margin-left: 5%; margin-top: 10px; border-radius: 2px; border:1px solid #50a4f4;}
.TwoDataNav span{float:left; width: 50%; text-align: center; color:#fff;}
.TwoDataNav span.cur{background: #50a4f4;}
.GridNav{float:left; width: 100%; height: 50px; line-height: 50px; color:#fff; display: flex;}
.GridNav li{float:left; flex: 1; height:30px; line-height: 30px; margin: 10px auto;}
.GridNav li:hover{background: none;}
.GridNav li.cur{color:#50a4f4;background: none; border-bottom:1px solid #50a4f4;}
.GridNavTable{float:left; width: 95%; height: 294px; overflow-y: auto; overflow-x: hidden; color:#000; margin-left: 5%;}
.GridNavTable table{float: left; width: 100%; height:auto; white-space: nowrap; border-top:1px solid #6cc6ff;border-left:1px solid #6cc6ff;}
.GridNavTable::-webkit-scrollbar{width:5px; position: absolute; height:10px;}
.GridNavTable::-webkit-scrollbar-track{background: rgba(128,128,128,0.5);border-radius: 5px;}
.GridNavTable::-webkit-scrollbar-thumb{background: rgba(77,156,227,0.6);border-radius: 10px;}
.GridNavTable::-webkit-scrollbar-thumb:hover{background: rgba(77,156,227,0.6);}
.GridNavTable::-webkit-scrollbar-corner{background: #179a16;}
.GridNavTable table tr td,.GridNavTable table tr th{border-bottom:1px solid #6cc6ff;height: 35px; line-height: 35px;border-right:1px solid #6cc6ff; text-align: center}
.GridNavTable table tr th{background: rgba(50,75,91,0.8);height: 40px; line-height: 40px;}
.GridNavTable table tr td{background: rgba(66,70,72,0.5);}

