.AlarmButton{position: fixed; bottom:100px; z-index: 6; right:20px; box-shadow: 0px 0px 18px #0087ff inset; background: rgba(0,0,0,0.66); border:2px solid #6cc6ff;width: 20px; height: 100px; padding: 5px 10px; font-size: 18px; line-height: 23px; color:#fff;}
.TwoAlarm{bottom:400px;}
.AlarmButton span,.AlarmContent ul li span em,.AlarmTotal ul li span em{width: 20px; height: 20px; background: #d81e06; border-radius: 90px; display: block; z-index: 1; top:-10px;position: absolute; right:-10px; font-size: 14px; text-align: center; line-height: 20px;}
.resourceMap{z-index: 1; top:110px; left:20px;}
.AlarmShow{position: fixed; bottom:10px; right:20px; width: 360px; height: 300px; border-radius: 15px; box-shadow: 0px 0px 18px #0087ff inset; background: rgba(0,0,0,0.66); border:2px solid #6cc6ff;font-size: 20px; line-height: 23px; color:#fff;}
.Two{ bottom:330px; z-index: 1;}
.AlarmShow h1{float:left; width: 100%; height: 35px; background: url("../images/Alarm/right.png") no-repeat right bottom; color:#ffb328; font-size: 18px; line-height: 35px;}
.AlarmShow h1 span{float:right; margin-right: 100px;}
.resourceMap h1{background: url("../images/Alarm/left.png") no-repeat left bottom;}
.resourceMap h1 span{float:left; margin-left: 100px;}
.But{background: url("../images/Alarm/but.png") no-repeat; width: 28px; height: 80px; position: absolute; top:110px; left:-29px;}
.AlarmContent,.AlarmTotal{float: left; width: 100%; height: 265px;}
.Total{position: absolute; background: url("../images/Alarm/r.png") no-repeat center center; background-size: 100% 100%; width: 110px; height: 110px; top:100px; left:130px; text-align: center; line-height: 25px;}
.Total h2{color:#fff; font-size: 18px; margin-top: 25px;}
.Total span{color:#ffe400; font-size: 20px;}
.Total span em{font-size: 12px;}
.AlarmContent ul,.AlarmTotal ul{float: left; width: 100%; height: 100%;}
.AlarmContent ul li{float: left;  position: relative; background: url("../images/Alarm/active.png") no-repeat center center; width: 102px; height:50px; margin-left: 25px; margin-top: 50px;}
.AlarmContent ul li:nth-child(even){margin-left: 115px;}
.AlarmContent ul li img{float: left; width: 24px; height: 24px; margin: 13px 10px;}
.AlarmContent ul li:nth-child(1) img{-webkit-filter: hue-rotate(330deg);filter: hue-rotate(330deg);}
.AlarmContent ul li:nth-child(2) img{-webkit-filter: hue-rotate(230deg);filter: hue-rotate(230deg);}
.AlarmContent ul li:nth-child(3) img{-webkit-filter: hue-rotate(150deg);filter: hue-rotate(150deg);}
.AlarmContent ul li span{font-size: 12px; text-align: center;}
.AlarmContent ul li span h3{font-size: 20px; line-height: 20px;}
.AlarmContent ul li:nth-child(1) span h3{color:#69ffec}
.AlarmContent ul li:nth-child(2) span h3{color:#cfff69}
.AlarmContent ul li:nth-child(3) span h3{color:#ff5f5f}
.AlarmContent ul li:nth-child(4) span h3{color:#ffac69}
.AlarmContent ul li:last-child span{line-height: 50px;}
.AlarmTotal ul{height:240px; overflow-x: hidden; overflow-y: auto;}
.AlarmTotal ul li{position:relative;float: left; background: url("../images/Alarm/active.png") no-repeat center center; width: 102px; height:50px; margin-top: 20px; margin-left: 13px;}
.AlarmTotal ul li img{float: left; width: 24px; height: 24px; margin: 13px 10px;}
.AlarmTotal ul li:nth-child(1) img{-webkit-filter: hue-rotate(330deg);filter: hue-rotate(330deg);}
.AlarmTotal ul li:nth-child(2) img{-webkit-filter: hue-rotate(230deg);filter: hue-rotate(230deg);}
.AlarmTotal ul li:nth-child(3) img{-webkit-filter: hue-rotate(150deg);filter: hue-rotate(150deg);}
.AlarmTotal ul li span{font-size: 12px; text-align: center;}
.AlarmTotal ul li span h3{font-size: 20px; line-height: 20px;}
.AlarmTotal ul li:nth-child(1) span h3{color:#69ffec}
.AlarmTotal ul li:nth-child(2) span h3{color:#cfff69}
.AlarmTotal ul li:nth-child(3) span h3{color:#ff5f5f}
.AlarmTotal ul li:nth-child(4) span h3{color:#ffac69;}
.AlarmTotal i,.AlarmList i{float: right; background: url("../images/Alarm/return.png") no-repeat center center; width: 18px; height: 18px; margin-right: 20px;}
.AlarmListTable{width: 100%; height: 240px;overflow-x: hidden; overflow-y: auto;}
.AlarmListTable::-webkit-scrollbar{width:5px; position: absolute; height:5px;}
.AlarmListTable::-webkit-scrollbar-track{background: rgba(128,128,128,0.5);border-radius: 5px;}
.AlarmListTable::-webkit-scrollbar-thumb{background: rgba(77,156,227,0.6);border-radius: 10px;}
.AlarmListTable::-webkit-scrollbar-thumb:hover{background: rgba(77,156,227,0.6);}
.AlarmListTable::-webkit-scrollbar-corner{background: #179a16;}
.AlarmList table{width: 100%; height: auto; border-collapse: collapse;margin: 0 auto;text-align: center; font-size: 14px; line-height: 40px;}
table td,table th{color: #c9c9c9;height: 40px;}
table thead th {width: 100px;font-size: 18px; color:#fff;}
.AlarmList table tr th,.AlarmList table tr td{width: 25%; height: 40px; overflow: hidden; text-overflow:ellipsis; white-space: nowrap;}
.AlarmListTable table tr td span.red{color:red;}
.AlarmListTable table tr td span.green{color:green;}
.ResourceMapList{float: left; margin: 15px 10px 0; width: 340px; height: auto; font-size: 18px; cursor: pointer;}
.ResourceMapList li{float: left; margin-left: 15px; box-shadow: 0px 0px 18px #0087ff inset; border-radius: 2px; background: rgba(0,0,0,0.66); border:1px solid #6cc6ff; width: 145px; height: 35px; margin-bottom: 10px; line-height: 35px; text-align: center;}
.ResourceMapList button{float: left; width: 310px; height: 45px; outline: none; background: url("../images/Alarm/title.png") no-repeat center center; color:#fff; font-size: 18px; background-size: 100% 100%;margin-left: 15px; border:0; margin-bottom: 10px;}
.ResourceMapList button i{background: url("../images/Alarm/Mapicon.png") no-repeat; vertical-align: middle; width: 16px; height: 16px; display: inline-block; margin-right: 8px;}
.ResourceMapList button span{display: inline-block; line-height: 30px;}
.ResourceMapTree{float: left; width: 310px; height: inherit; font-size: 16px; margin: 15px 25px; overflow: hidden;}
.ResourceMapTree h2{text-align: center; width: 100%; color:#ffb328; font-size: 18px;}
.ResourceMapTree ul{float: left; width: 310px; height: auto; overflow: hidden;}
.ResourceMapTree ul li{float: left; width: 100%; line-height: 25px; cursor: pointer;}
.ResourceMapTree ul li em{display: inline-block; position: absolute; right:30px;}
.ResourceMapTree ul li span{float: left;}
.ResourceMapTree ul li span i{float: left; margin-right: 5px; margin-top: 9px; width: 0; height: 0;border-top: 7px solid transparent;border-left: 7px solid white;border-bottom: 7px solid transparent;}
.ResourceMapTree ul li span i.only{float: left; margin-right: 5px; margin-top: 9px; width: 0; height: 0;border-left: 7px solid transparent;border-right: 7px solid transparent;border-top: 7px solid white;}
.ResourceMapTree ul li > ul{padding-left: 20px; }
.ResourceMapTreeUl{float: left; width: 310px; height:96%; overflow: auto; position: relative;}
.ResourceMapTreeUl::-webkit-scrollbar{width:5px; position: absolute; height:5px;}
.ResourceMapTreeUl::-webkit-scrollbar-track{background: rgba(128,128,128,0.5);border-radius: 5px;}
.ResourceMapTreeUl::-webkit-scrollbar-thumb{background: rgba(77,156,227,0.6);border-radius: 10px;}
.ResourceMapTreeUl::-webkit-scrollbar-thumb:hover{background: rgba(77,156,227,0.6);}
.ResourceMapTreeUl::-webkit-scrollbar-corner{background: #179a16;}
.resourceMap{display:flex; flex-direction: column;}
.Search{position: fixed; top:120px; right:20px; width: 360px; height: 36px;  background: rgba(0,0,0,0.66); border:1px solid #6cc6ff; border-radius: 2px;}
.Search input{float:left;width: 295px; height: 36px; padding: 0 4px; color:#fff; font-size: 16px;}
.Search button{float:left;width: 57px; height: 36px; background: #4d9ce3; outline: none;border:0; padding:0;}
.Search button i{background:url("../images/Alarm/search.png") no-repeat center center; width: 20px; height: 20px; display: inline-block;}
